<template>
  <div id="schedule">
    <!-- ( )  //      . -->
    <h3>. [{{user.userComName}}] - {{user.userName}}</h3>
    <br>

    <div>

      <v-dialog v-model="dialog" persistent max-width="600px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn x-large color="primary" dark v-bind="attrs" v-on="on">
             
          </v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">[{{com_name}}]   </span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-select :items="[2021]" label="" required v-model="new_recruit.reYear"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select :items="['', '']" label="" required v-model="new_recruit.reFlag"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select :items="['', '']" label=" " required v-model="new_recruit.reStatus"></v-select>
                </v-col>

                <v-col cols="12" sm="6">
                  <v-text-field v-model="dateRangeText" label="  " prepend-icon="mdi-calendar" readonly>
                  </v-text-field>
                  <div>  : {{ dates[0] }}</div>
                  <div>  : {{ dates[1] }}</div>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-date-picker v-model="dates" range></v-date-picker>

                </v-col>


              </v-row>
            </v-container>
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">
              Close
            </v-btn>
            <v-btn color="blue darken-1" text @click="createRecruit">
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <br>
      <br>



        <div>
              .
        </div>


      <v-simple-table fixed-header height="300px" class="mt-5">
        <thead>
          <tr>
            <th class="text-center">No</th>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
            <th class="text-center"></th>
          </tr>
        </thead>
        <tbody>
          <!-- reSeq   .(   )  -->
          <tr v-for="(recruit, index) in getRecruitListLately" :key="index" class="text-center">
            <td>{{ index+1 }}</td>
            <!-- <td>{{ recruit.reSeq }}</td> -->
            <td>{{ recruit.reYear }} {{ recruit.reFlag }}</td>
            <td>{{ recruit.reStatus }}</td>
            <td>{{ recruit.reStartDate }} ~ {{recruit.reEndDate }}</td>
            <td>
              <!-- <v-btn :to="{ name: 'Progress', params: { recruitNo: recruit.reSeq } }" ></v-btn> -->
              <v-btn @click="goToProgress(recruit)"></v-btn>
            </td>
            <td>
              <v-btn @click="goToProgress(recruit.reSeq)"></v-btn>
            </td>

          </tr>
        </tbody>
      </v-simple-table>

    </div>

  </div>

</template>

<script>
  import {
    mapState,
    mapGetters
  } from "vuex";

  export default {
    name: "Schedule",
    data: function () {
      return {
        com_name: "",
        dialog: false,
        myReno: '',

        dates: ['2021-01-01', '2021-01-01'],
        new_recruit: {
          reYear: '',
          reFlag: '',
          reStatus: '',
          reStartDate: '',
          reEndDate: '',
        },
      }
    },

    methods: {
      createRecruit: function () {
        this.dialog = false

        // this.dialog = false
        // console.log("createRecruit !")
        // console.log(":",this.new_recruit)

        this.new_recruit.reStartDate = this.dates[0]
        this.new_recruit.reEndDate = this.dates[1]

        this.$store
          .dispatch("INSERT_RECRUIT", this.new_recruit)
          .then(() => console.log("insertRecruit"))

        // axios.post , DB      . 
        //     (seq) , state  . 
        //this.$store.dispatch('addRecruit',    res.data)

      },
      goToProgress: function (recruit) {
        this.myReno = recruit.reSeq
        this.$store.state.selectedRecruitNo = recruit.reSeq
        console.log("goToProgres!!", this.$store.state.selectedRecruitNo)
        this.$router.push({
          name: 'Menu',
          params: {
            recruitNo: recruit.reSeq,
            recruitInfo: recruit
          }
        })
      },


    },

    created: function () {
      this.$store
        .dispatch("GET_RECRUIT_LIST")
        .then(() => console.log("getRecruitList"))
      console.log(this.$store.state.recruitList);
    },
    computed: {
      ...mapState(["recruitList", "user"]),
      ...mapGetters(["getRecruitListLately"]),

      dateRangeText() {
        return this.dates.join(' ~ ')
      },

    },
  }
</script>

