<template>
  <div id="createset" class="mt-12 mx-14">
    <div class="h4">  </div>
    <!--  Form -->
    <v-row justify="center" no-gutters>
      <v-col cols="10" class="mt-10">
        <!--    -->
        <v-col>
          <v-row justify="space-around" no-gutters>
            <v-col
              v-for="(item, index) in seasonData"
              :key="index"
              :cols="item.col"
            >
              <v-select :items="item.data" :label="item.name" solo hide-details disabled>
              </v-select>
            </v-col>
          </v-row>
        </v-col>
        <!--    -->
        <v-col class="mt-5 mb-0 h5"> </v-col>
        <v-col>
          <v-row class="brd" style="justify-between" no-gutters>
            <v-col
              v-for="(item, index) in groupForm"
              :key="index"
              :cols="item.col"
              class="d-flex"
            >
              <v-col class="d-flex justify-center align-center pa-0">
                <span class="subtitle-1">{{ item.name }}</span>
              </v-col>
              <v-col v-if="item.data" cols="8" class="d-flex align-center">
                <v-autocomplete
                  v-model="formData[item.value]"
                  :items="item.data"
                  :label="item.name"
                  solo
                  :multiple="item.multiple"
                  hide-details
                  dense
                ></v-autocomplete>
              </v-col>
            </v-col>
            <v-col class="d-flex justify-center align-center" cols="4">
              <v-dialog v-model="dialog" max-width="500px">
                <!--    -->
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="secondary"
                    v-bind="attrs"
                    v-on="on"
                  >
                     
                  </v-btn>
                </template>
                <!--   Modal -->
                <v-card>
                  <v-card-title>
                     
                  </v-card-title>
                  <v-card-text>
                    <v-row v-for="i in this.formData.groupNum" :key="i" no-gutters>
                      <v-col v-for="j in viewTypesNum" :key="j">
                        <v-select
                          :items="formData.viewTypes"
                          solo
                        ></v-select>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-dialog>
            </v-col>
            <v-col cols="3" class="d-flex justify-center align-center">
              <v-btn
                class="ma-2"
                :loading="loading"
                :disabled="loading"
                color="secondary"
                @click="[loader = 'loading', addSchedule()]"
              >
                 
              </v-btn>
            </v-col>
          </v-row>
        </v-col>
        
        
        <hr>
        <!--  -->
        <v-data-table
          :headers="schGroupTable.headers"
          :items="schGroupTable.schGroups"
          single-expand
          :expanded.sync="schGroupTable.expanded"
          item-key="schGroupNo"
          @click:row="(item, slot) => slot.expand(!slot.isExpanded)"
        >
          <!-- <tbody>
            <template v-slot:body="{ items }">
            <tr v-for="item in items" :key="item.schGroupNo" @click="props.expanded = !props.expanded">
              <td>{{ props.item.schGroupNo }}</td>
              <td>{{ props.item.schGroupDate }}</td>
              <td>{{ props.item.schGroupTime }}</td>
              <td>{{ props.item.schGroupCareer }}</td>
              <td>{{ props.item.schGroupInterview }}</td>
              <td>{{ props.item.schGroupViewee }}</td>
              <td>{{ props.item.schGroupGuide }}</td>
              <td>{{ props.item.schGroupViewer }}</td>
            </tr>
          </template>
          </tbody> -->
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
              {{ item }}
            </td>
          </template>
          <!-- <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
              {{ item }}
            </td>
          </template> -->
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from 'axios'
import _ from "lodash"

const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: "CreateSet",
  props: {
    recruitNo: [Object, String, Number]
  },
  data() {
    return {
      dialog: false,
      //  
      seasonForm: [
        {
          name: "",
          data: ["2020 ", "2021 ", "2021 "],
          col: 2,
        },
        { name: "", data: ["", "", ""], col: 2 },
        { name: "", data: ["", "", "", ""], col: 2 },
        { name: "", data: [], col: 3 },
      ],
      //   
      groupForm: [
        {
          name: "",
          data: ["", "IT", ""],
          col: 3,
          multiple: false,
          value: 'part'
        },
        {
          name: "",
          data: ["SW ", "", ""],
          col: 3,
          multiple: false,
          value: 'career'
        },
        {
          name: " ",
          data: ["", "", "PT", ""],
          col: 5,
          multiple: true,
          value: 'viewTypes'
        },
        {
          name: "",
          col: 2
        },
        {
          name: "",
          data: [],
          col: 2,
          multiple: false,
          value: 'startDate'
        },
        {
          name: "",
          data: ["13", "14"],
          col: 2,
          multiple: false,
          value: 'startTime'
        },
        {
          name: "",
          col: 2
        },
        {
          name: "",
          data: [],
          col: 2,
          multiple: false,
          value: 'endDate'
        },
        {
          name: "",
          data: ["19", "20"],
          col: 2,
          multiple: false,
          value: 'endTime'
        },
        {
          name: " ",
          col: 2
        },
        {
          name: " ",
          data: [2, 3],
          col: 3,
          multiple: false,
          value: 'viewerNum'
        },
        {
          name: " ",
          data: [8, 10],
          col: 3,
          multiple: false,
          value: 'vieweeNum'
        },
        {
          name: " ",
          data: [1, 2],
          col: 3,
          multiple: false,
          value: 'duration'
        },
        {
          name: "  ",
          col: 2,
        },
        {
          name: " ",
          data: [1, 2, 3, 4],
          col: 3,
          mutiple: false,
          value: 'vieweePerGroup'
        },

      ],
      //   V-Model
      seasonData: 1,
      formData: {
        // 
        part: '',
        // 
        career: '',
        //  
        viewTypes: [],
        //  , 7/10
        startDate: '',
        //  
        startTime: '',
        //  (  )
        endDate: '',
        //  
        endTime: '',
        //    , int
        viewerNum: 0.1,
        //    , int
        vieweeNum: 0.1,
        //    , int
        duration: '',
        //  
        blindView: false,
        //     , int
        vieweePerGroup: 0.1,
        //     ( )
        groupNum: 0
      },
      //  
      schGroupTable: {
        expanded: [],
        headers: [
          {
            text: 'No.',
            align: 'start',
            sortable: false,
            value: 'schGroupNo',
          },
          { text: '', value: 'schGroupDate' },
          { text: '', value: 'schGroupTime' },
          { text: '', value: 'schGroupCareer' },
          { text: ' ', value: 'schGroupInterview' },
          { text: '', value: 'schGroupViewee' },
          { text: '', value: 'schGroupGuide' },
          { text: '', value: 'schGroupViewer' },
        ],
        //  
        schGroups: [
          {
            //   seq
            schGroupNo: 1,
            //    
            schGroupDate: '7/10',
            //    
            schGroupTime: '14:00',
            //   
            schGroupCareer: '',
            //    
            schGroupInterview: ['', 'PT', ''],
            //   ()
            schGroupViewee: ['', '', '', ''],
            //   
            schGroupGuide: [],
            //   
            schGroupViewer: [{
              name: '',
            }],
            //   
            schSmallGroups: [
              {
                schSmallGroupNo: 1,
                //   
                schSmallGroupInterview: ['', 'PT', ''],
                //   ()
                schSmallGroupViewee: ['', ''],
              },
              {
                schSmallGroupNo: 2,
                schSmallGroupInterview: ['', '', 'PT'],
                schSmallGroupViewee: ['', ''],
              },
            ]
          },
          {
            schGroupsNo: 2,
            schGroupsDate: '7/10',
            schGroupsTime: '16:00',
            schGroupsCareer: '',
            schGroupsInterview: [', PT, '],
            schGroupsViewee: [],
            schGroupsGuide: [],
            schGroupsViewer: [],
            schGroup: [
              {
                schGroupNo: 1,
                schGroupsCareer: '',
                schGroupsInterview: ['', 'PT', ''],
                schGroupsViewee: '',
              }
            ]
          },
          {
            schGroupsNo: 3,
            schGroupsDate: '7/10',
            schGroupsTime: '17:00',
            schGroupsCareer: 'SW',
            schGroupsInterview: ['', '', 'PT'], 
            schGroupsViewee: [],
            schGroupsGuide: [],
            schGroupsViewer: [],
            schGroup: [
              {
                schGroupNo: 1,
                schGroupsCareer: 'SW',
                schGroupsInterview: ['PT', '', ''],
                schGroupsViewee: '',
              }
            ]
          },
        ]
      },
      

      loader: null,
      loading: false,
    }
  },
  methods: {
    //   
    addSchedule: function () {
      // axios.post(`https://localhost:8080/groupAll/divide` + this.recruitNo, this.formData)
      axios.post(`${SERVER_URL}/groupAll/divide/` + this.recruitNo, this.formData)
        .then(res => {
          console.log(res)
          axios.get(' ')
            .then(res => console.log(res))
            .catch(err => console.log(err))
          axios.get('  ')
            .then(res => console.log(res))
            .catch(err => console.log(err))
          axios.get('  ')
            .then(res => console.log(res))
            .catch(err => console.log(err))
          axios.get('  ')
            .then(res => console.log(res))
            .catch(err => console.log(err))
        })
        .catch(err => console.log(err))
    },
    clicked: function (value) {
      this.schGroupTable.expanded.push(value)
    }
  },
  watch: {
    loader() {
      const l = this.loader
      this[l] = !this[l]

      setTimeout(() => (this[l] = false), 3000)

      this.loader = null
    },
    makeGroup() {
      _.toInteger()
      this.formData.groupNum = _.ceil(this.formData.vieweeNum/this.formData.vieweePerGroup)
    },
    
  },
  computed: {
    makeGroup() {
      return [this.formData.vieweePerGroup, this.formData.vieweeNum]
    },
    viewTypesNum() {
      return _.findLastIndex(this.formData.viewTypes) + 1
    },
  },
  created () {
  }
}
</script>

