<script>
import axios from "axios"

// const SERVER_URL = "https://localhost:8080/"
// const SERVER_URL = "https://i4a405.p.ssafy.io:8080"
const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: "Signup",
  components: {},
  data: () => ({
    userInfo: [
      { label: "", type: "text", value: "hrEmail" },
      { label: " ", type: "text", value: "hrEmailCode" },
      { label: "", type: "text", value: "hrName" },
      { label: "", type: "password", value: "hrPassword" },
      {
        label: " ",
        type: "password",
        value: "hrPasswordConfirmation",
      },
      { label: "", type: "text", value: "hrPhone" },
      { label: "", type: "text", value: "comName" },
    ],
    credentials: {
      hrEmail: "",
      hrEmailCode: "",
      hrName: "",
      hrPassword: "",
      hrPasswordConfirmation: "",
      hrPhone: "",
      comName: "",
      hrCertified: "",
      agreed: false,
    },
    certifiedNum: "",
    isCertified: false,
  }),
  methods: {
    //  
    send: function() {
      if (this.credentials.hrEmail) {
        axios
          .post(`${SERVER_URL}/email/send`, this.credentials)
          .then((res) => {
            this.certifiedNum = res.data
            alert("  ")
          })
          .catch((err) => {
            console.log(err)
            alert("  ")
          })
      }
    },

    //  
    certified: function() {
      console.log(":" + this.credentials.hrEmailCode)
      console.log(":" + this.certifiedNum)
      if (this.credentials.hrEmailCode == this.certifiedNum) {
        this.isCertified = true
        alert(" ")
      } else {
        alert(" ")
        this.isCertified = false
      }
    },

    signup: function() {
      if (
        this.credentials.hrPassword != this.credentials.hrPasswordConfirmation
      ) {
        alert(" .")
        return
      }

      let regexp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i
      if (!regexp.test(this.credentials.hrEmail)) {
        alert("   .")
        return
      }

      if (this.isCertified == false) {
        alert("  .")
        return
      }

      if (this.credentials.agreed) {
        axios
          .post(`${SERVER_URL}/hr/signup`, this.credentials)
          .then((res) => {
            console.log(res)
            alert(" ")
            this.$router.push({ name: "Home" })
          })
          .catch((err) => {
            console.log(err)
            alert("   .")
          })
      } else {
        alert("    .")
      }
    },
  },
}
</script>

<style scoped>
.info-table {
  background-color: transparent !important;
}
</style>

