<template>
  <div class="recruit">
    <!--########   ##############-->
    <v-container>
      <v-row>
        <!-- <v-col cols="10"> -->

        <!--########   ###############-->
        <!--######## if  recruitNo        -->
        <v-app-bar>
          <v-tabs style="width: initial" align-with-title>
            <v-tab :to="{ name: 'Progress', params: { recruitNo: recruitNo } }">
              reseq:{{ recruitNo }}  </v-tab>
            <!-- <v-tab>
              <router-link :to="{ name: 'Viewers' }"> </router-link>
            </v-tab> -->
            <v-tab :to="{ name: 'Viewers', params: { recruitNo: recruitNo } }"> </v-tab>
            <v-tab :to="{ name: 'Viewees', params: { recruitNo: recruitNo } }"> </v-tab>
          </v-tabs>
          <v-chip style="" outlined>
            <router-link :to="{ name: 'CreateSet', params: { recruitNo: recruitNo }}">
               
            </router-link>
          </v-chip>
        </v-app-bar>

        <!-- </v-col> -->
      </v-row>
    </v-container>
    <router-view />
  </div>
</template>

<script>
  export default {
    name: "Recruit",
    data: function () {
      return {
        selectedItem: 1,
        tabs: [{
            text: " ",
          },
          {
            text: " ",
          },
          {
            text: " ",
          },
        ],
      }
    },
    props: {
      recruitNo: [Object, String, Number],
      selectRecruitTrigger: [Object, String, Number],
    },
    created: function () {
      console.log("reno:", this.recruitNo)
    },
    watch: {
      recruitNo: function () {
        if (this.$route.path != "/recruitmenu/progress") {
          this.$router.push({
            name: "Progress"
          })
          // if (this.$route.path != "/recruitmenu") {
          //   this.$router.go(1)
        }
      },
    },
  }
</script>

