<template>
  <div id="waitRoom">
    <!--  .     .  -->

    <!-- -->
    <div>
      <v-app-bar color="deep-purple accent-4" dense dark>
        <div class="d-flex">
          <v-toolbar-title class="mx-3">   : __ </v-toolbar-title>
          <v-toolbar-title class="mx-3">  : </v-toolbar-title>
        </div>

        <div class="float-right">
          <v-toolbar-title class="mx-3">
            {{ com_name }}
            {{ re_year }}
            {{ re_flag }}
            {{ re_status }}
            <!--    -->
            <input
              class="btn"
              type="button"
              @click="leaveSession"
              value="                                                                                                                                                                                                                                              
               "
            />
          </v-toolbar-title>
        </div>
      </v-app-bar>
    </div>

    <!--   .  -->
    <v-container>
      <!--row1. :  ,   -->
      <v-row> </v-row>

      <!-- row2 : , , FAQ  row-->
      <v-row>
        <!-- row2[] :  -->
        <v-col cols="3">
          <user-video :stream-manager="mainStreamManager" />
          <!-- <ManagerList /> -->
        </v-col>
        <!-- row2[] :  -->
        <v-col cols="6">
          <user-video
            :stream-manager="publisher"
            @click.native="updateMainVideoStreamManager(publisher)"
          />
          <user-video
            v-for="sub in subscribers"
            :key="sub.stream.connection.connectionId"
            :stream-manager="sub"
            @click.native="updateMainVideoStreamManager(sub)"
          />
          <!-- <VieweeList /> -->
        </v-col>
        <!-- row2[] :  FAQ   -->
        <v-col cols="3"></v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { OpenVidu } from "openvidu-browser";
import UserVideo from "@/components/room/UserVideo";
import axios from "axios";

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

// import { mapState } from "vuex";

// import ManagerList from "@/components/room/ManagerList.vue"
// import VieweeList from "@/components/room/VieweeList.vue"

export default {
  name: "WaitRoom",
  components: {
    UserVideo,
    // ManagerList,
    // VieweeList,
  },
  data: function() {
    return {
      OV: undefined,
      session: undefined,
      mainStreamManager: undefined, //  
      publisher: undefined, //  
      subscribers: [],

      // From SessionController
      sessionName: undefined,
      token: undefined,
      userName: "",
      type: undefined, //  (manager) / (interviewer) / (interviewee)

      // From Main.vue
      com_name: undefined,
      re_year: undefined,
      re_flag: undefined,
      re_status: undefined,
    };
  },
  mounted() {
    this.com_name = this.$route.params.interview.comName;
    this.re_year = this.$route.params.interview.recruitYear;
    this.re_flag = this.$route.params.interview.recruitFlag;
    this.re_status = this.$route.params.interview.recruitStatus;
    this.sessionName = this.$route.params.interviewer.sessionName;
    this.token = this.$route.params.interviewer.token;
    this.userName = this.$route.params.interviewer.interviewerName;
    this.type = this.$route.params.interviewer.type;

    this.OV = new OpenVidu();
    this.session = this.OV.initSession();

    this.session.on("streamCreated", ({ stream }) => {
      const subscriber = this.session.subscribe(stream);
      this.subscribers.push(subscriber);
    });

    this.session.on("streamDestroyed", ({ stream }) => {
      const index = this.subscribers.indexOf(stream.streamManager, 0);
      if (index >= 0) {
        this.subscribers.splice(index, 1);
      }
    });

    this.session
      .connect(this.token, { clientData: this.userName })
      .then(() => {
        let publisher = this.OV.initPublisher(undefined, {
          audioSource: undefined, // The source of audio. If undefined default microphone
          videoSource: undefined, // The source of video. If undefined default webcam
          publishAudio: true, // Whether you want to start publishing with your audio unmuted or not
          publishVideo: true, // Whether you want to start publishing with your video enabled or not
          resolution: "320x240", // The resolution of your video
          frameRate: 30, // The frame rate of your video
          insertMode: "APPEND", // How the video is inserted in the target element 'video-container'
          mirror: false, // Whether to mirror your local video or not
        });

        this.mainStreamManager = publisher;
        this.publisher = publisher;

        console.log(this.userName);
        console.log(this.token);

        this.session.publish(this.publisher);
      })
      .catch((error) => {
        console.log(
          "There was an error connecting to the session:",
          error.code,
          error.message
        );
      });

    // window.addEventListener("beforeunload", this.leaveSession);
  },

  methods: {
    leaveSession() {
      if (this.session) this.session.disconnect();

      this.session = undefined;
      this.mainStreamManager = undefined;
      this.publisher = undefined;
      this.subscribers = [];
      this.OV = undefined;

      axios
        .get(`${SERVER_URL}/session/leaveSession`, {
          params: {
            sessionName: this.sessionName,
            token: this.token,
          },
        })
        .then((res) => {
          console.log(res);
          this.$router.push({
            name: "Main",
          });
        })
        .catch((err) => {
          console.log(err);
          console.log("  !");
        });
      // window.removeEventListener("beforeunload", this.leaveSession);
    },

    updateMainVideoStreamManager(stream) {
      //    
      if (this.mainStreamManager === stream) return;
      this.mainStreamManager = stream;
    },
  },

  created: function() {
    // if (this.viewerLogin) {
    //   this.$router.push({ name: "ViewerRecruitItem" })
    // }
  },

  computed: {
    // ...mapState(["whoLogin"]),
  },
};
</script>

<style scoped>
#standbyRoom {
  background-color: white;
  /* width: 100vw; */
  height: 100vh;
}
</style>
